@import url(https://fonts.googleapis.com/css?family=Lato:300,400,700,900);

@import "reset";
@import "mixins_font_face";
@import "variables";

html {
  font-size: 16px;

  @media screen and (max-width:1141px) {
    font-size: 15px;
  }

  @media screen and (max-width:481px) {
    font-size: 13px;
  }

  @media screen and (max-width:425px) {
    font-size: 11px;
  }
}

body {
  -webkit-font-smoothing: antialiased; // fix cross-browser font-weight problem
  -moz-osx-font-smoothing: grayscale; // fix cross-browser font-weight problem on firefox

  background-color: black;
}

.keyword {
  color: $orange;
}

.hidden {
  display: none !important;
}

.vert_centered {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}

section {
  position: relative;

  width: 100%;
  height: 100%;

  max-width: 1142px;
  min-height: 700px;

  margin: 0 auto 0 auto;

  @media screen and (max-width:1141px) {
    max-width: 920px;
    min-height: 600px;
  }

  @media screen and (max-width:481px) {
    min-height: 480px;
  }
}

.home {
  text-align: center;

  .logo {
    width: 14em;
  }

  .title {
    margin-top:0.8em;

    display: block;
    font-family: $lato;
    font-weight: 700;
    font-size: 2.7em;
    letter-spacing: 0.08em;
    color: white;

    @media screen and (max-width:481px) {
      font-size: 2.2em;
    }
  }

  .subtitle {
    margin-top:0.8em;
    padding: 0 24px;

    display: block;
    font-family: $lato;
    font-weight: 300;
    font-size :2em;
    line-height: 1.2em;
    color: white;

    @media screen and (max-width:481px) {
      font-size: 1.8em;
    }
  }
}

.icon_container_skills {
  padding-top: 1.5em;

  img {
    margin: 0 7px;
  }
}

.icon_container_social {
  padding-top: 1.5em;

  img {
    margin: 0 4px;
  }
}

.tech {
  height: 3em;
  position: relative;
}

.social {
  height: 1.4em;
  opacity: 0.25;
}

.social:hover {
  opacity: 1.0;
}

.email {
  margin-top: 2em;

  font-family: $lato;
  font-weight: 300;
  font-size :1.4em;
  line-height: 1.2em;
  color: white;
}

#subtitle_break {
  @media screen and (max-width:602px) {
    display: none;
  }
}

#webgl-plane {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;

  z-index: -1;
}

.polygon_webgl {
  background: radial-gradient(ellipse, #050408 70%, #0f0b16);
}

.polygon_image {
  background-image: url('../res/img/bg_1.png');
  background-size: cover;
}

.path {
  stroke-dasharray: 421;
  stroke-dashoffset: 421;
  animation: dash 1.7s ease-out forwards;
}

@keyframes dash {
  to {stroke-dashoffset: 0;}
}
